:root {
  --trust-green: #205c40;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: #f8f9fa;
  margin: 0;
}

.sidebar {
  width: 200px;
  height: 100vh;
  background-color: var(--trust-green);
  padding-top: 2rem;
  position: fixed;
}

.sidebar a {
  display: block;
  padding: 12px 20px;
  color: #ffffff;
  text-decoration: none;
}

.sidebar a:hover,
.sidebar a.active {
  background-color: #184b34;
  border-left: 4px solid #ffffff;
}

.main {
  margin-left: 200px;
  padding: 2rem;
}

.tool-card {
  border-radius: 12px;
  padding: 1rem;
  background: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  text-align: center;
  transition: transform 0.2s ease;
  position: relative;
  cursor: pointer;
  border: 2px solid var(--trust-green);
}

.tool-card:hover {
  transform: translateY(-5px);
}

.tool-card img {
  width: 80px;
  height: 80px;
  object-fit: contain;
  border-radius: 50%;
  margin-bottom: 1rem;
  border: 2px solid var(--trust-green);
  background-color: #fff;
}

.tool-card .badge {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: var(--trust-green);
}
